import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from "@/views/Login";
import Register from "@/views/Register";

import adminHome from "@/views/admin/adminHome";
import welcomeAdmin from "@/views/admin/welcomeAdmin";

import userHome from "@/views/user/userHome";
import welcomeUser from "@/views/user/welcomeUser";

Vue.use(VueRouter)

const routes = [
  {
    path:"/",
    redirect:'/login'
  },
  {
    path:"/login",
    component: Login
  },
  {
    path:"/register",
    component: Register
  },
  {
    path: "/adminHome",
    component: adminHome,
    redirect: "/welcomeAdmin",
    children: [
        {path: "/welcomeAdmin",component: welcomeAdmin},

    ]
  },
  {
    path:"/userHome",
    component: userHome,
    redirect: "/welcomeUser",
    children: [
        {path: "/welcomeUser",component: welcomeUser},

    ]
  },

];

const router = new VueRouter({routes});

