<template>
    <div>
        <!--登陆框-->
        <div id="div_loginBox">
            <el-row>
                <el-col><h1 style="text-align: center">—— 个人理财系统 ——</h1></el-col>
            </el-row>
            <!--登陆表单-->
            <el-form ref="ref_login" :model="formData" :rules="form_rules" label-width="0px" class="formData">
                    <!--账号输入框-->
                    <el-form-item class="form_input"  prop="username">
                        <el-input v-model="formData.username" placeholder="请输入您的用户名" prefix-icon="el-icon-user"></el-input>
                    </el-form-item>
                <!--密码输入框-->
                    <el-form-item class="form_input" prop="password">
                        <el-input v-model="formData.password" placeholder="请输入密码" prefix-icon="el-icon-key" show-password></el-input>
                    </el-form-item>
                <!--图片验证码-->
                <el-row>
                    <el-col :span="15">
                        <el-form-item class="form_input" prop="verifyCode">
                            <el-input v-model="formData.verifyCode" placeholder="请输入验证码" prefix-icon="el-icon-key" show-password></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8" :offset="1" style="height: 100%">
                        <img src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=2310890073,3469009192&fm=26&gp=0.jpg"/>
                    </el-col>
                </el-row>
                <!--登陆按钮-->
                <el-button type="primary" @click="Button_Login">登陆</el-button>
                <el-link :underline="false" @click="Button_Register">没有账号？去注册</el-link>
            </el-form>
        </div>
    </div>

</template>

<script>
    export default {
        name: "Login",
        data(){
            return {
                //表单账号和密码框
                formData:{
                    username:'',
                    password:'',
                    verifyCode:'',
                },
                //表单验证规则
                form_rules:{
                    username:[
                        {required:true,message:'请输入账号',trigger:'blur'},
                        {min:12,max:12,message:'账号长度为12个字符！',trigger:'blur'}
                    ],
                    password:[
                        {required:true,message:'请输入密码',trigger:'blur'},
                        {min:6,message:'密码长度必须大于6！',trigger:'blur'}
                    ],
                    verifyCode:[
                        {required:true,message:'请输入验证码',trigger:'blur'},
                    ]
                },
            }
        },
        methods: {
            Button_Login(){
                this.$refs.ref_login.validate((valid) => {
                    if (!valid) {
                        this.$message({
                            message: '请输入合法的账号密码以及正确的验证码',
                            type: 'warning'
                        });
                        return;
                    }
                    this.$request1({
                        url: 'login/verify',
                        method: 'post',
                        headers: {
                            'Content-Type': 'application/json;charset=UTF-8'
                        },
                        data: {
                            username: this.formData.username,
                            password: this.formData.password
                        }
                    }).then(res => {
                        console.log(res);
                        let data = res.data;
                        switch (data.code) {
                            // 账户验证成功
                            case "SUCCESS":
                                this.$message({
                                    message: '登陆成功',
                                    type: 'success'
                                });
                                console.log(data);
                                // 挂载用户信息
                                window.$user.type = data.user.type;
                                window.$user.id = data.user.id;
                                // 转跳到主页
                                this.$router.replace('/home');
                                break;
                            // 账户或密码错误
                            case "failure":
                                this.$message({
                                    message: '账户或密码错误',
                                    type: 'error'
                                });
                                break;
                            // 验证码错误
                            case "verifyCode":
                                this.$message({
                                    message: '验证码错误，请重新输入',
                                    type: 'error'
                                });
                                break;
                            // 其他错误
                            default:
                                this.$message({
                                    message: '未知错误',
                                    type: 'error'
                                });
                                break;
                        }
                    }).catch(err => {
                        this.$message({
                            message: '在与服务器通讯的过程中捕捉到错误：' + err,
                            type: 'error'
                        });
                    })
                });
            },
            Button_Register() {
                this.$router.replace('/register');
            }
        }
    }
</script>

<style lang="less" scoped>
    .center() {
        position: absolute;//绝对定位
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    #div_loginBox {
        background-color: #ffffff;
        width: 500px;
        height: 400px;
        border-radius: 20px;
        box-shadow: 0 0 1px 1px rgba(0,0,0,0.05) !important;
        .center();
        background-color: rgba(255,255,255,0.7);

        .formData {
            position: absolute;
            width: 100%;
            box-sizing: border-box;
            padding-left: 50px;
            padding-right: 50px;

            img {
                width: 100%;
                height: 40px;
            }

            .el-button {
                width: 100%;
                background-color: #33CABB;
            }

            .el-link {
                color: #33CABB;
                text-align: center;
                width: 100%;
                padding-top: 20px;
            }
        }




    }
</style>

